p {
    font-family: Arial, Helvetica, sans-serif;
}

.page-header {
    background-color: #f2f2f2;
    padding-top: 50px;
    padding-bottom:55px;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;

    border-bottom: 6px solid #ffc000;

    margin-bottom: 6px;
}

.header-left {
    flex-grow: 5;
}

.header-right {
    flex-grow: 1;
}

#name, #job {
    font-size: 24px;
    margin: 6px;
    line-height: 0.8;
}

.contacts {
    font-size: 12px;
    margin-top: 12px;
}

.section {
    margin-left: 20px;
    margin-right: 20px;
    display: flex;
}

.section p {
    font-size: 18px;
}

.section-title {
    font-style: italic;
    font-weight: bold;
    
    flex: 1.2;
}

.section-content {
    flex: 4;
    line-height: 1.2;
}

hr {
    margin-left: 20px;
    margin-right: 20px;
}